.skills__section {
    padding: 100px 0 160px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #fafafa;
}

.skills__container-card {
    background: linear-gradient(45deg, #31de79 0%, #53c582 100%);
    box-shadow: 0 6px 20px rgba(111, 255, 111, 0.2);
    width: 280px;
    height: 500px;
    text-decoration: none;
    border-radius: 4px;
}

.skills__container-card:nth-child(2) {
    transform: scale(1.05);
    background: #242424;
    margin: 0 24px;
}

.skills__container-card:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
}

.skills__container {
    display: flex;
    justify-content: center;
    align-content: center;
}

.skills__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
}

.skills__heading {
    color: #1c2237;
    margin-bottom: 24px;
}

.skills__container-cardInfo {
    display: flex;
    flex-direction: column;
    height: 500px;
    padding: 24px;
    align-items: center;
    color: #fff;
}

h3 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 36px;
    color: #fafafa;
}

.skills__container-features {
    margin: 16px 0 32px;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skills__container-features li {
    margin-bottom: 10px;
    color: #fff;
    font-size: 24px;
}

.icon {
    margin: 24px 0;
}

.card-img {
    width: 150px;
    height: 150px;
    margin: auto;
    align-items: center;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 960px) {
    .skills__container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
    }
    
    .skills__container-card {
        width: 90%;
    }

    .skills__wrapper {
        margin: 0 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .skills__container-card:nth-child(2) {
        transform: scale(1);
        background: #242424;
        margin: 24px;
    }

    .skills__container-card:hover {
        transform: none;
    }
}